

























export default {
	data() {
		return {
			lists: [{
					id: 'view',
					name: '视图容器',
					open: false,
					pages: [
						'view',
						'scroll-view',
						'swiper',
						'movable-view',
					]
				}, {
					id: 'content',
					name: '基础内容',
					open: false,
					pages: ['text', 'rich-text', 'icon', 'progress']
				}, {
					id: 'form',
					name: '表单组件',
					open: false,
					pages: ['button', 'checkbox', 'form', 'input', 'label', 'picker', 'picker-view', 'radio',
						'slider',
						'switch', 'textarea'
					]
				}, {
					id: 'nav',
					name: '导航',
					open: false,
					pages: ['navigator']
				}, {
					id: 'media',
					name: '媒体组件',
					open: false,
					pages: ['image', 'audio', 'video']
				}, {
					id: 'map',
					name: '地图',
					open: false,
					pages: ['map']

				},
				{
					id: 'web-view',
					name: '网页',
					open: false,
					pages: ['web-view'],

				},
			]
		}
	},
	onLoad() {
















	},
	onShareAppMessage() {
		return {
			title: '欢迎体验uni-app',
			path: '/pages/tabBar/component/component'
		}
	},
	onNavigationBarButtonTap(e) {
		uni.navigateTo({






		})
	},
	methods: {
		trigerCollapse(e) {
			if(!this.lists[e].pages){
				this.goDetailPage(this.lists[e].url);
				return ;
			}
			for (var i = 0; i < this.lists.length; ++i) {
				if (e === i) {
					this.lists[i].open = !this.lists[e].open;
				} else {
					this.lists[i].open = false;
				}
			}
		},
		goDetailPage(e) {
			if (typeof e === 'string') {
				uni.navigateTo({
					url: '/pages/component/' + e + '/' + e
				})
			} else {
				uni.navigateTo({
					url: e.url
				})
			}
		}
	}
}



